.popup__close {
  position: absolute;
  right: -40px;
  top: -40px;
  width: 32px;
  height: 32px;
  background-image: url(../../../images/Close_Icon.svg);
}

@media screen and (max-width: 750px) {
  .popup__close {
    right: 0;
    top: -36px;
    width: 20px;
    height: 20px;
  }
}
